@font-face {
  font-family: 'Raleway';
  src: url('fonts/raleway-semibold-webfont.woff2') format('woff2'), url('fonts/raleway-semibold-webfont.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'Raleway';
  src: url('fonts/raleway-medium-webfont.woff2') format('woff2'), url('fonts/raleway-medium-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Raleway';
  src: url('fonts/raleway-light-webfont.woff2') format('woff2'), url('fonts/raleway-light-webfont.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'Noto';
  src: url('fonts/notoserif-regular-webfont.woff2') format('woff2'), url('fonts/notoserif-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Noto';
  src: url('fonts/notoserif-bold-webfont.woff2') format('woff2'), url('fonts/notoserif-bold-webfont.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}
html {
  font-size: 100%;
}
* {
  quotes: '\201C' '\201D' '\2018' '\2019';
}
body {
  font-family: Noto, Georgia, serif;
  line-height: 1.6;
  -webkit-text-size-adjust: 100%;
  font-feature-settings: "kern", "liga";
  -webkit-font-smoothing: antialiased;
  color: #12353C;
  background-color: #fff;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: Raleway, sans-serif;
  color: #12353C;
  margin: 0;
  padding: 0;
  line-height: 1.1;
}
h1,
.h1 {
  font-weight: 300;
  font-size: 2.25rem;
}
h2,
.h2 {
  font-weight: 600;
  font-size: 1.25rem;
  margin-bottom: 1rem;
}
p {
  margin: 0 0 1rem;
}
p + p:last-child {
  margin-bottom: 0;
}
body {
  margin: 0;
}
main {
  margin: 1rem 0.5rem;
}
a,
.link {
  color: inherit;
  text-decoration: none;
  transition: color 250ms ease;
}
a svg,
.link svg {
  vertical-align: middle;
}
a path,
.link path {
  transition: fill 250ms ease;
}
a:visited,
.link:visited {
  color: inherit;
}
a:focus,
.link:focus,
a:hover,
.link:hover {
  color: #0CAFCD;
}
a:focus *,
.link:focus *,
a:hover *,
.link:hover * {
  color: inherit;
}
a:focus path,
.link:focus path,
a:hover path,
.link:hover path {
  fill: #0CAFCD;
}
.link-text {
  vertical-align: middle;
}
label {
  margin-bottom: 0.5rem;
}
input {
  margin-bottom: 1rem;
  padding: 0.5rem;
  min-width: 160px;
  border: 1px solid #B3B3B3;
  font-size: 1rem;
}
fieldset {
  padding: 1rem 0.5rem;
}
input,
label {
  display: block;
}
textarea {
  min-width: 240px;
  min-height: 80px;
  display: block;
  font-size: 1rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
}
button,
.button {
  display: inline-block;
  border: none;
  margin-bottom: 1em;
  padding: 0.5rem 1rem;
  background-color: #eddd3e;
  color: #12353c;
  font-size: 1rem;
  text-decoration: none;
}
button:hover,
button:focus,
.button:hover,
.button:focus {
  background-color: #e3d015;
}
.card {
  display: block;
  margin-bottom: 1rem;
}
.card__title {
  font-weight: 600;
  font-size: 1.25em;
}
.card__meta {
  color: #B3B3B3;
  margin-bottom: 1rem;
}
.card__description {
  padding-bottom: 1rem;
  border-bottom: 2px solid #EDDD3E;
}
.card__actions a:not(:last-child) {
  margin-right: 1rem;
}
.item {
  display: block;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #EDDD3E;
}
.item__title {
  font-weight: 600;
  font-size: 1.25em;
  margin-bottom: 1rem;
}
.item__title a {
  border-bottom: none;
}
.item__meta {
  color: #B3B3B3;
}
header {
  padding: 1rem;
  overflow: hidden;
  background-color: #EDDD3E;
}
header a {
  color: #12353C;
}
.app-logo-container {
  display: inline-block;
  margin-right: 0.5rem;
  width: 2.75rem;
  vertical-align: middle;
}
.app-logo-container img {
  max-width: 100%;
}
.app-title {
  display: inline-block;
  margin: 0;
  font-size: 2.25rem;
  font-weight: bold;
  vertical-align: middle;
}
